@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

.NavBar {
    background: linear-gradient(100deg, #05192e 0%, #0c2a33 100%);
}

.NavBrand,
.NavLink {
    color: whitesmoke;
    text-decoration: none;
}

.NavBrand {
    font-family: 'Poppins';
    font-size: 1.6rem;
}

.NavBrandIcon {
    margin-left: -0.2rem;
    margin-bottom: 1rem;    
}

.NavLink {
    font-size: 1.1rem;
    text-align: center;
    margin: 0.5rem 0.5rem;
    padding: 0.5rem 0.5rem;
    border-top: 1px solid #00000000;
    border-bottom: 1px solid #00000000;
}

.NavLink:hover {
    color: #05192e;
    background-color: whitesmoke;
    border-radius: 5rem;
}

.ActiveNavLink {
    border-top: 1px solid goldenrod;
    border-bottom: 1px solid goldenrod;
}

.NavLinkIcon {
    font-size: 1.5rem;
    padding-right: 0.5rem;
}